import { Canvas } from '@react-three/fiber';
import React, { Component } from 'react';
import { OrbitControls , Stars } from '@react-three/drei';
function Box() {
    return (
        <mesh>
            <boxBufferGeometry attach="geometry" />
            <meshLambertMaterial attach="material" />
        </mesh>
    )
}
class Three2 extends Component {
    state = {  }
    componentDidMount = () => {
        document.querySelector(".head").style.height = window.innerHeight + "px";
        console.log(window.innerHeight);
    }
    render() { 
        return ( 
            <div className="Three2">
                <div className="back">
                    <Canvas className="head">
                        <Stars/>
                        <OrbitControls/>
                        <ambientLight intensity={0,1} />
                        <spotLight position={[10,15,10]} angle={0,3} />

                        <Box/>
                    </Canvas>
                </div>
                
                <div className="body">
                    <div className="header">
                            <div className="log"></div>
                            <ul>
                                <li>one</li>
                                <li>two</li>
                                <li>three</li>
                                <li>four</li>
                                <li>five</li>
                            </ul>
                    </div>
                    <div className="start">
                        <div className="left">
                            <h1>hello</h1><h3>i'am abd</h3>
                            <p>fjsadjf sdjf sdhf jdsh fkh d</p>
                        </div>
                        <div className="right">
                            <h3>skilles</h3>
                            <h3>eduction</h3>
                            <h3>contact</h3>
                            <h3>about</h3>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Three2;